html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

.container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.brand h1 {
  font-size: 2.4rem;
  text-transform: uppercase;
  color: white;
}

.brand h1 span {
  font-size: 2.4rem;
  color: crimson;
}

.logo {
  width: 2.4rem;
  height: 2.4rem;
  border-radius: 50%;
}

#landing-left {
  .left-part {
    // background-color: #b5b9ca;
    background-color: navajowhite;
    .ant-typography {
      margin-bottom: 1rem;
      margin-top: 0;
    }
    a {
      color: black;
      // font-weight: bold;
    }
    a:hover {
      color: @primary-color;
    }
  }
  .right-part {
    position: relative;
    z-index: 1;
  }
  .right-part::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.2;
    // z-index: -1;
  }
}

/* End left landing */

/* Introduction */
#introduce {
  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 48px;
    background-color: #f39a9a;

    .ant-typography {
      color: white;
      text-align: justify;
    }
  }
}
/*End Introduction*/

/* Do Cake */
#do-cake {
  position: relative;
  // display: flex;
  // justify-content: center;
  // // align-items: center;
  // background-image: url(~assets/img/docake.jpg);
  // background-size: contain;
